@import url(https://fonts.googleapis.com/css?family=Oranienbaum&subset=latin,cyrillic);
body {
    margin: 0;
    padding: 0;
    background-color: #D8D8D8;
    /*overflow-y: hidden;*/
}



.page {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    align-items: center;
    height: 100vh;
}

.player {
    display: block;
}

.logo
{
}

.panel {
    width: 373px;
    display: block;
    /*height: 350px;*/
    /*flex-grow: 0;*/
    /*flex-shrink: 0;*/
    /*flex-basis: 0;*/
}

.page__preloader {
    display: block;
    width: 120px;
    height: 120px;
    background: url(../images/gears.svg);
    background-size: contain;
}

.player__button {
    width: 50px;
    height: 50px;
    cursor: pointer;
    margin: 5px;
    & g {
        fill: white;
    }
    &:hover g {
        fill: #FFDB4D;
    }
}

.player__controls {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    align-items: flex-start;
    background-color: black;
}

.player__screen
{
}

.page_hidden {
    display: none;
    pointer-events: none;
    opacity: 0;
}

.page_content_form
{
}

.page_content_player {}

.logo__img {
    display: inline-block;
    position: absolute;
    top: 0;
    right: 0;
    color: #969696;
    padding: 0;
    margin: 0;
}

.logo__title {
    font-family: sans-serif;
    font-size: 35px;
    max-width: 245px;
    color: #969696;
}

.panel__confirm {
    display: block;
    width: 200px;
    height: 50px;
    margin: 0 auto;
    font-size: 18px;
    background-color: #FFDB4D;
    border: none;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    cursor: pointer;
    transition-duration: 0.4s;
    border-radius: 8px;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.1);
    &:hover {
        background-color: #F6CC26;
        color: white;
    }
}

.panel__input {
    display: block;
    box-sizing: border-box;
    width: 353px;
    margin: 20px auto;
    font-family: Arial, sans-serif;
    font-size: 18px;
    color: #969696;
    font-weight: 500;
    appearance: none;
    box-shadow: none;
    border-radius: none;
    padding: 10px;
    border: solid 1px #dcdcdc;
    border-bottom: solid 2px #c9c9c9;
    transition: border 0.3s;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.1);
    &:focus {
        outline: none;
        border-bottom: solid 2px #969696;
    }
}

.panel__logo {
    position: relative;
    margin-bottom: 40px;
}

